.round-image-box {
    width: 250px;
    height: 250px; 
    border-radius: 50%; 
    overflow: hidden;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); 
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    background-color: white;
  }
  
  .round-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  