.project-btn {
    border-radius: 5px;
    border-width: 1px;
}

.project-btn .ant-menu-title-content {
    color : #55a52d;
}

.project-btn .ant-menu-item:hover::after{
    border-bottom-color: #55a52d !important;
}

.navbar{
    margin-bottom: 30px;
    border-bottom-right-radius: 18px;
    border-bottom-left-radius: 18px;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.4);
    background-color: white;
    transition: 0.2s;
    cursor:pointer;
}