.contact-section {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: center;
    justify-items: center;
    align-items: center;
  }
  
  .contact-box {
    display: flex;
  }

  .contact-logo {
    display: grid;
    margin-top: 50px;
    justify-items: center;
  }

  @media screen and (max-width:800px) {
    .contact-section{
        grid-template-columns: repeat(1, 1fr);
    }
  }