.contact-box{
    padding: 75px;
    width: 200px;
    flex-direction: column;
    justify-content: center;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.2);
    border-radius: 12px;
    align-items: center;
    text-align: center;
    background-color: white;
    margin: 25px;
    transition: 0.2s;
    cursor:pointer;
}

.contact-box:hover {
    transform: scale(1.05);
    transition: 0.2s;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.8);
  }

  .contact-box img{
    height: 94px;
    width: 94px;
  }

  @media screen and (max-width: 1250px) {
    .contact-box{
        padding: 30px;
        width: 150px;
    }

    .contact-box img{
        height: 48px;
        width: 48px;
      }
  }
